import React from 'react'
import { ReactComponent as Robot } from '@/assets/images/robot.svg'

class Home extends React.Component {
  state = {
    happy: 50,
    sad: 50,
    text: '',
    moodUp: '',
    moodDown: '',
    isFrozen: false,
    list: []
  }

  id = null
  tId = null
  old = this.state.happy
  count = 0

  handleHappy = this.handleHappy.bind(this)
  handleSad = this.handleSad.bind(this)
  handleDistance = this.handleDistance.bind(this)
  handleMouseDown = this.handleMouseDown.bind(this)
  handleUp = this.handleUp.bind(this)
  handleKeyDown = this.handleKeyDown.bind(this)
  handlePrintHappy = this.handlePrintHappy.bind(this)

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (this.state.moodUp.length === 6) {
      this.setState({
        moodUp: '',
      })
    }
    if (this.state.moodDown.length === 6) {
      this.setState({
        moodDown: '',
      })
    }
  }

  handlePrintHappy() {
    clearInterval(this.tId)
    console.log(this.state.happy)
    if (this.state.happy === 100) {
      this.tId = setInterval(() => {
        this.setState({ list: [...this.state.list, "I'm extremely happy !!!"] })
      }, 500)
    } else if (this.state.happy >= 90 && this.state.happy <= 99) {
      this.tId = setInterval(() => {
        this.setState({ list: [...this.state.list, "I'm extremely happy !!!"] })
      }, 1000)
    } else {
      this.setState({ list: [] })
    }
  }

  handleHappy(e) {
    let distance = parseInt(e.target.value) - this.state.happy
    this.setState(
      { 
        happy: parseInt(e.target.value), 
        sad: 100 - e.target.value,
        moodUp: (function(_this) {
          // If down first time, stop
          if (distance < 0 && !_this.state.moodUp.length) {
            return ''
          }
          // If up and last character is not '1'
          if (
            distance > 0 &&
            _this.state.moodUp[_this.state.moodUp.length - 1] !== '1'
          ) {
            return _this.state.moodUp + '1'
          } else if (
            distance < 0 &&
            _this.state.moodUp[_this.state.moodUp.length - 1] !== '0'
          ) {
            // If down and last character is not '0'
            return _this.state.moodUp + '0'
          }
          return _this.state.moodUp
        })(this),
      }, () => {
        // Print stop messing with happy sensor
        if (this.state.moodUp === '101010') {
          this.setState({
            text: '​Hey operator, stop messing with my Happy (or Sad) sensor​',
            isFrozen: true,
          })
          setTimeout(() => {
            this.setState({
              isFrozen: false,
            })
          }, 3000)
        }
        this.handlePrintHappy()
      }
    )
  }

  handleSad(e) {
    let distance = parseInt(e.target.value) - this.state.sad
    this.setState(
      { 
        sad: parseInt(e.target.value), 
        happy: 100 - e.target.value,
        moodDown: (function(_this) {
          // If down first time, stop
          if (distance < 0 && !_this.state.moodDown.length) {
            return ''
          }
          // If up and last character is not '1'
          if (
            distance > 0 &&
            _this.state.moodDown[_this.state.moodDown.length - 1] !== '1'
          ) {
            return _this.state.moodDown + '1'
          } else if (
            distance < 0 &&
            _this.state.moodDown[_this.state.moodDown.length - 1] !== '0'
          ) {
            // If down and last character is not '0'
            return _this.state.moodDown + '0'
          }
          return _this.state.moodDown
        })(this),
      }, () => {
        // Print stop messing with happy sensor
        if (this.state.moodDown === '101010') {
          this.setState({
            text: '​Hey operator, stop messing with my Happy (or Sad) sensor​',
            isFrozen: true,
          })
          setTimeout(() => {
            this.setState({
              isFrozen: false,
            })
          }, 3000)
        }
        this.handlePrintHappy()
      }
    )
  }

  handleDistance() {
    let distance = this.state.sad - this.old
    if (distance >= 30) {
      console.log('broken heart')
      this.setState({ text: 'Alarm, our robot has a broken heart'})
    }
    this.old = this.state.sad
  }

  handleMouseDown() {
    this.id = setInterval(() => {
      this.handleDistance()
    }, 1000);
  }

  handleUp() {
    this.handleDistance()
    clearInterval(this.id)
  }

  handleKeyDown() {
    this.count++
    if (this.count > 1) {
      return
    }
    console.log('begin key')
    this.handleDistance()
  }

  render() {
    const listMess = this.state.list.map((item, index) => (
      <p className="message-list__items" key={index}>
        {item}
      </p>
    ))
    const isDisabled = this.state.isFrozen ? true : false
    return (
      <div className="container">
        {this.state.isFrozen === true && <div className="ui-frozen"/>}
        <div className="emo-control">
          <div className="range-slider">
            <span className="range-slider__enotion">Happy:</span>
            <input className="range-slider__range" 
                   type="range" 
                   value={this.state.happy} 
                   onChange={this.handleHappy}
                   onMouseDown={this.handleMouseDown}
                   onMouseUp={this.handleUp}
                   onKeyDown={this.handleKeyDown}
                   onKeyUp={this.handleUp}
                   disabled={isDisabled}
            />
            <span className="range-slider__value">{this.state.happy}</span>
          </div>
          <div className="range-slider">
            <span className="range-slider__enotion">Sad:</span>
            <input className="range-slider__range" 
                   type="range" 
                   value={this.state.sad} 
                   onChange={this.handleSad}
                   onMouseDown={this.handleMouseDown}
                   onMouseUp={this.handleUp}
                   onKeyDown={this.handleKeyDown}
                   onKeyUp={this.handleUp}
                   disabled={isDisabled}
            />
            <span className="range-slider__value">{this.state.sad}</span>
          </div>
        </div>
        <div className="emo-display">
          <div className="message-list">
            {listMess.length > 0 ? listMess : this.state.text}
          </div>
          <Robot />
        </div>
      </div>
    )
  }
}

export default Home
